body {
  font-family: "Mitr", sans-serif;
}



.button {
  background-color: #5271ff;
  border: none;
  color: white;
  padding: 16px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 20px;
  margin: 4px 2px;
  transition-duration: 0.4s;
  cursor: pointer;
}

.button1 {
  background-color: white;
  color: black;
  /* border: 2px solid #5271ff; */
}

.button1:hover {
  background-color: #5271ff;
  color: white;
}


.image-frame {
  display: inline-block;
  border: 3px solid #000;
  padding: 5px;
  margin: 5px;
  background-color: rgba(0, 0, 0, 0.3);
}

.video-frame {
  display: inline-block;
  border: 3px solid #000;
  padding: 5px;
  margin: 5px;
  background-color: rgba(0, 0, 0, 0.3);
}
